.x-scene {
  position: relative;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  perspective: 1000px; }
  .x-scene-lcontrol {
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 100%;
    left: -100%; }
  .x-scene-rcontrol {
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 100%;
    right: -100%; }

.x-carousel {
  width: 100%;
  height: 100%;
  position: absolute;
  transform-style: preserve-3d;
  transition: transform 0s; }
  .x-carousel__slide {
    position: absolute; }
    .x-carousel__slide img {
      width: 100%;
      height: 100%; }

.x-slide {
  width: 100%;
  height: 100%; }
